// src/App.js
import React, { useState, useEffect } from 'react';
import Login from './Components/Login';
import './App.css';
import Navbar from './Components/Navbar';
import TextForm from './Components/TextForm';
import "bootstrap/dist/css/bootstrap.min.css";

function App() {
  const [user, setUser] = useState(null);
  const [mode, setMode] = useState('dark');

  const toggleMode = () => {
    if (mode === 'light') {
      setMode('dark');
      document.body.style.backgroundColor = 'black';
      document.body.style.color = 'white';
    } else {
      setMode('light');
      document.body.style.backgroundColor = 'white';
      document.body.style.color = 'black';
    }
  };

  useEffect(() => {
    document.body.style.backgroundColor = mode === 'dark' ? 'black' : 'white';
    document.body.style.color = mode === 'dark' ? 'white' : 'black';
  }, [mode]);

  const handleLogin = (userData) => {
    // Set user data in state upon successful login
    setUser(userData);
  };

  const handleLogout = () => {
    setUser(null);
  };

  if (!user) {
    // Not logged in: show the login form
    return <Login onLogin={handleLogin} />;
  }

  // Logged in: show some "protected" content
  return (
    <div className="App">
      <h1>Welcome, {user.TextForm}Prashant Kaushik</h1>
      <button onClick={handleLogout}>Logout</button>

      {/* Rest of your app (e.g., text utilities) goes here */}
      <Navbar title="TextUtils" mode={mode} toggleMode={toggleMode} />

      <div className="container my-3">
        <TextForm heading="Enter the text to Analyze below" />
      </div>
    </div>
  );
}

export default App;
